.Info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 16px;
  padding-bottom: 16px;
}

.PriceInfo {
  flex-shrink: 0;
}

.Price {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
}

.OldPrice {
  color: var(--text_secondary);
  text-decoration: line-through;
}

.Name {
  font-size: 17px;
  line-height: 22px;
  margin-right: 8px;
}
