:root {
  --green: #5cba47;
  --accent: #59499e;
  --field_background_custom: white;
  --button_commerce_background: var(--green);
  --header_background: var(--accent);
  --search_bar_background: #f4f4fb;
  --background_page: #f4f4fb;
  --text_secondary: #948daa;
  --text_primary: #222222;
  --icon_secondary: #948daa;
  --delimiter: rgba(148, 141, 170, 0.4);
  --segmented_control_tint: var(--accent);
  --button_outline_border: var(--accent);
  --button_outline_foreground: var(--accent);
  --header_tab_active_background: white;
  --header_tab_active_text: var(--accent);
}

.Input.Input--ios > .Input__border {
  background-color: var(--field_background_custom);
}

.Input input {
  color: var(--accent);
}
