.Doctor {
  display: flex;
  flex-direction: row;
  padding-bottom: 12px;
}

.Avatar {
  height: 74px;
  width: 74px;
  flex-shrink: 0;
  margin-right: 12px;
}

.Info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
}

.Info div {
  margin-right: 4px;
}

.Price {
  line-height: 22px;
  font-size: 17px;
  font-weight: 500;
}

.Pricing span {
  margin-right: 8px;
}

.OldPrice {
  font-size: 12px;
  color: #222222;
  text-decoration-line: line-through;
  opacity: 0.5;
}

.Rating {
  display: flex;
  flex-direction: row;
}
