.About {
  padding-top: 8px;
}

.Header {
  color: var(--text_secondary);
  font-size: 15px;
  line-height: 20px;
  padding: 0px 16px;
  margin-bottom: 8px;
}

.Description {
  padding: 16px;
  line-height: 24px;
}

.Description,
.List {
  margin-bottom: 16px;
}

.Header,
.List {
  padding: 0px 16px;
}

.List,
.Description {
  background: white;
}

.List > div {
  border-bottom: 0.5px solid var(--delimiter);
}

.List > div:last-child {
  border-bottom: none;
}
