.Header {
  padding: 16px;
  background-color: white;
  margin: 8px 0;
  display: flex;
  flex-direction: row;
}

.Form {
  padding: 5px;
  margin-bottom: 96px;
  background-color: white;
}

.Form > .Title {
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--text_secondary);
  margin: 10px;
}

.Avatar {
  width: 88px;
  height: 88px;
  margin-right: 16px;
}

.Info > .Title {
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--text_secondary);
  margin-bottom: 2px;
}

.Info > .Value {
  font-size: 17px;
  line-height: 22px;
  font-weight: 600;
}

.Info:nth-child(1) {
  margin-bottom: 8px;
}

.Description {
  font-size: 11px;
  line-height: 13px;
  color: var(--text_secondary);
  text-align: center;
}

.OrderButtonArea {
  padding: 16px;
}

.OrderButtonArea .Button {
  margin-bottom: 16px;
}
