.Slots {
  display: flex;
  flex-direction: row;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  margin-right: -16px;
  margin-left: -16px;
  padding-left: 16px;
  padding-right: 16px;
}

.Description {
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 13px;
  color: var(--text_secondary);
}

.Slot {
  margin-right: 8px;
}

.Slot:last-child {
  padding-right: 16px;
}
