.DateSelect {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: white;
  padding: 16px;
}

.Header {
  font-size: 13px;
  line-height: 18px;
  color: var(--text_secondary);
  margin-bottom: 8px;
}

.Title {
  font-size: 17px;
  line-height: 22px;
  font-weight: 500;
}

.Icon {
  flex-shrink: 0;
  display: flex;
  color: var(--icon_secondary);
}

.Icon > div {
  margin: auto 0;
}

.DateSelect select {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
