@keyframes appear {
  from {
    transform: scale(0.85);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.DoctorCard {
  position: relative;
  background: #ffffff;
  animation: appear;
  animation-duration: 0.25s;
  animation-timing-function: ease-out;
  /* Shadow, 200 */
  box-shadow: 0 16px 32px -8px lightgray;
  border-radius: 8px;
  padding: 16px;
  padding-bottom: 0px;
  display: flex;
  flex-direction: column;
  transition: background 0.2s ease-out, box-shadow 0.2s ease-out;
}

.DoctorCard:active:not(:focus-within) {
  box-shadow: 0 8px 16px -8px lightgray;
  background: whitesmoke;
}

.Doctor {
  border-bottom: 0.5pt solid #ced1db;
}

.Clinic > div {
  border-bottom: 0.5pt solid #ced1db;
}

.Clinic:last-child > div:last-child {
  border-bottom: none;
}
