.Location {
  flex-direction: row;
  display: flex;
  justify-content: space-between;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 12px;
  color: #222222;
  line-height: 16px;
}

.Address {
  margin-bottom: 4px;
}

.Stations {
  flex-direction: row;
  display: flex;
  flex-wrap: wrap;
}

.Stations > div {
  margin-bottom: 4px;
}

.Icon {
  flex-shrink: 0;
  color: var(--green);
}
