.Photo {
  position: relative;
  padding-top: 62.5%;
  width: 100%;
  background: url(../../svg/AvatarPlaceholder.svg);
  background-size: 25%;
  background-position: center;
  background-repeat: no-repeat;
}

.Photo > img {
  position: absolute;
  top: 0;
  width: 100%;
  object-fit: cover;
  object-position: top;
}
